.hs-blog-post {
  background-color: #fcfcfc;

  .kl-navbar .kl-navbar--action-button {
    background: var(--light-grey-1, #f2f2f0);
  }

  .blog-header--post {
    margin-top: 25px;
    border-radius: 30px;
    border: 2px solid var(--dark-grey-1, #1c1c1c);
    box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1);
    margin-bottom: 59px;
  }

  .blog-post {
    padding-top: 0;
    max-width: 765px;
    margin: auto;
  }

  .blog-post__body-wrapper {
    max-width: none;
  }

  .body-container--blog-post {
    max-width: 950px;
    margin: auto;
  }

  .blog-post__preheader {
    display: none;
  }

  .blog-post__title {
    color: var(--dark-grey-1, #1c1c1c);
    text-align: center;
    font-size: 48px;
    font-style: normal;
    font-weight: 800;
    line-height: 54px;
    max-width: none;
    margin: 0 auto 16px;
  }

  .blog-post__summary {
    color: var(--dark-grey-1, #1c1c1c);
    text-align: center;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
    max-width: none;
  }
  .blog-post__meta {
    width: 100%;
    justify-content: center;

    .blog-post__avatar,
    .blog-post__author a {
      display: none;
    }
  }

  .blog-post__timestamp {
    color: var(--dark-grey-1, #1c1c1c);
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    opacity: 0.5;
    margin-bottom: 44px;
  }

  .blog-post__divider {
    display: none;
  }

  .blog-post__body {
    margin-bottom: 40px;
    padding-bottom: 60px;
    border-bottom: 2px solid var(--dark-grey-1, #1c1c1c);

    p,
    li {
      color: var(--dark-grey-1, #1c1c1c);
      text-align: justify;
      font-size: 14px;
      line-height: 24px;
    }

    h2 {
      color: var(--dark-grey-1, #1c1c1c);
      font-size: 36px;
      font-style: normal;
      font-weight: 800;
      line-height: 42px;
    }

    h3 {
      color: var(--dark-grey-1, #1c1c1c);
      font-size: 24px;
      font-style: normal;
      font-weight: 800;
      line-height: 32px;
      text-decoration-line: underline;
    }
  }

  .kl-testimonials {
    .kl-testimonial__quote svg {
      color: var(--dark-grey-1, #1c1c1c);
      font-size: 80px;
      font-style: normal;
      font-weight: 800;
      line-height: 24px; /* 30% */
      opacity: 0.1;
    }
  }

  .blog-post__tags {
    &::before {
      content: "Tags";
      display: flex;
      align-items: center;
      justify-content: center;

      margin-right: 26px;

      color: var(--dark-grey-1, #1c1c1c);

      font-size: 18px;
      font-style: normal;
      font-weight: 800;
      line-height: normal;
    }

    .blog-post__tag-link {
      border-radius: 10px;
      background: var(--light-grey-1, #f2f2f0);
      padding: 8px 25px;
      margin-bottom: 10px;
      margin-right: 10px;

      color: var(--dark-grey-1, #1c1c1c);
      text-align: center;
      font-size: 18px;
      font-style: normal;
      line-height: normal;
      text-decoration-line: underline;
    }
  }

  .blog-more {
    h2 {
      color: var(--dark-grey-1, #1c1c1c);
      text-align: center;
      font-size: 48px;
      font-style: normal;
      font-weight: 800;
      line-height: 54px; /* 112.5% */
      margin-bottom: 52px;

      @media (max-width: 768px) {
        margin-bottom: 30px;
      }
    }

    .blog-more-posts {
      padding-top: 0;
    }
  }

  .blog-more-posts__post-wrapper {
    @include postOnListing(".blog-more-posts");
    @media (max-width: 768px) {
      padding: 15px 0;
    }
  }
}
