@mixin postOnListing($className: ".blog-index") {
  #{$className}__post {
    border-radius: 20px;
    border: 2px solid var(--dark-grey-1, #1c1c1c);
    background: var(--white-1, #fcfcfc);
    box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1);
    padding: 20px;
    height: 486px;
    cursor: pointer;
  }

  #{$className}__post-image,
  .blog-more-posts__post-image {
    width: 100%;
    height: 170px;
    padding: 0;
    border-radius: 10px;
    margin-bottom: 16px;
  }

  #{$className}__post-date.digiphy-custom {
    margin-left: 0;
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    opacity: 0.5;
    margin-bottom: 14px;
    display: block;
  }

  #{$className}__post-content {
    padding: 0;

    h3 {
      color: var(--dark-grey-1, #1c1c1c);
      font-size: 24px;
      font-style: normal;
      font-weight: 800;
      line-height: 28px;
      margin-bottom: 20px;
    }

    p {
      margin-bottom: 0;
      color: var(--dark-grey-1, #1c1c1c);
      font-size: 12px;
      font-style: normal;
      line-height: 18px;
    }
  }

  #{$className}__post-content h3 a {
    display: -webkit-box;
    -webkit-line-clamp: var(--title-max-lines);
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  #{$className}__post-meta #{$className}__post-author {
    display: none;
  }

  #{$className}__post-meta,
  .blog-more-posts__post-meta {
    margin-top: 0;
  }

  #{$className}__post-preheader.digiphy-custom {
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-decoration-line: underline;
    margin-bottom: 0;
    text-transform: none;
  }
}
