@mixin input {
  color: var(--dark-grey-1, #1c1c1c);
  font-size: 18px;
  font-style: normal;
  line-height: normal;
}

@mixin buttonWrapper {
  border-radius: 52px;
  border: 2px solid var(--dark-grey-1, #1c1c1c);
  background: var(--dark-grey-1, #1c1c1c);
  padding: 15px 25px;
  min-width: 198px;
  &:hover {
    color: var(--dark-grey-1, #1c1c1c) !important;
    background-color: var(--light-grey-1, #f2f2f0);
    border-color: var(--dark-grey-1);
    transform: none;
    transform: translate(2px, -4px);
    box-shadow: -2px 3px #1c1c1c;
    * {
      color: var(--dark-grey-1, #1c1c1c) !important;
      text-decoration: none;
      transform: none;
    }
  }
}

@mixin buttonText {
  color: var(--light-grey-1, #f2f2f0) !important;
  text-align: center;
  font-size: 18px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

.kl-card {
  border-radius: 20px;
  box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1);
  border: 2px solid var(--dark-grey-1, #1c1c1c);
  padding: 28px 25px;
  display: flex;

  h3 {
    text-decoration: none;
  }

  .hs-form-field {
    margin-bottom: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    text-decoration: none;
    margin-bottom: 13px;
  }

  p {
    margin-bottom: 22px;
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
  }

  img {
    width: 194px;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    margin-right: 30px;
  }

  .image-wrapper {
    flex-shrink: 0;
  }
  .content-wrapper {
    flex: 1;
  }

  .kl-card__image {
    margin-bottom: 0;
  }

  input[type="submit"] {
    @include buttonWrapper;
    @include buttonText;
  }

  .button {
    @include buttonWrapper;
    span {
      @include buttonText;
    }
  }
}

.simple-card {
  background: var(--light-blue-1, #a3deff);
  .cta_button {
    @include buttonWrapper;
    @include buttonText;
    span {
      @include buttonText;
    }
  }
}

.callout-card {
  background: var(--light-grey-1, #f2f2f0);
}

.newsletter-card {
  background: var(--green-1, #73bfa3);

  input[type="email"] {
    @include input;
    border-radius: 52px;
    border: 2px solid var(--dark-grey-1, #1c1c1c);
    background: var(--light-grey-1, #f2f2f0);
    height: 60px;
    margin-bottom: 13px;
    padding: 20px 35px;

    &::placeholder {
      @include input;
    }
  }
}

.subscribe-card {
  border-radius: 30px;
  padding: 38px 40px 25px;
  background: linear-gradient(264deg, #ffb74a 0%, #ffcf4a 100.36%);
}
