@import "./listing.scss";
@import "./post.scss";
@import "./post-card.scss";

// GENERAL

body {
  --dark-grey-1: #1c1c1c;
  --white-1: #fcfcfc;
  --light-grey-1: #f2f2f0;
  --title-max-lines: 3;
}

.hidden {
  display: none !important;
}

.blog-subscribe {
  border-radius: 40px;
  border: 2px solid #1c1c1c;
  background: var(--purple-1, #7b6acc) !important;
  box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1);
  padding: 67px 36px;
  display: flex;
  justify-content: center;
  align-items: center;

  max-width: 1212px;
  margin: auto;
  margin-bottom: -175px;
  height: 330px;
  z-index: 1;
  position: relative;

  .blog-subscribe__form {
    display: none;
  }

  h1 {
    color: var(--light-grey-1, #f2f2f0);
    text-align: center;
    font-size: 48px;
    font-weight: 800;
    line-height: 54px; /* 112.5% */
    margin-bottom: 41px;
  }

  .blog-subscribe__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: none;
  }

  .cta_button {
    margin-bottom: 15px;
  }
}

// MENU
.kl-header {
  background-color: transparent;
  position: relative;
  padding: 35px 0;
  box-shadow: none;
}

.kl-navbar {
  justify-content: space-between;
  max-width: 1310px;
  height: auto;

  .kl-navbar--action-button {
    width: 126px;
    height: 46px;
    flex-shrink: 0;
    border-radius: 50px;
    background: var(--white-1, #fcfcfc);
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0;
    gap: 12px;
    padding: 0;
    transform: none;

    p {
      color: var(--dark-grey-1, #1c1c1c);
      font-size: 18px;
      font-style: normal;
      font-weight: 800;
      line-height: normal;
      margin-bottom: 0;
    }

    &:focus {
      outline: 0;
    }

    &:hover {
      text-decoration: none;
    }
  }

  .kl-navbar--action-button_icon {
    margin: 0;
  }

  .kl-navbar__logo {
    margin-right: 0;
  }

  .kl-navbar__content {
    flex: initial;
  }

  .kl-navbar__button {
    color: var(--dark-grey-1, #1c1c1c);
    text-align: center;
    font-size: 18px;
    font-style: normal;
    font-weight: 800;
    padding: 12px 26px;
    border-radius: 50px;
    line-height: normal;
    background-color: transparent;

    &:hover {
      background-color: #fff;
    }
  }

  .kl-navbar__button > span {
    padding: 0;
    &:after {
      display: none;
    }
  }

  .kl-navbar__end > a + a,
  .kl-navbar__end > a:not(:first-child) {
    border-radius: 50px;
    border: 2px solid var(--dark-grey-1, #1c1c1c);
    background: var(--purple-1, #7b6acc);
    padding: 10px 26px;
    margin-left: 10px;

    color: var(--light-grey-1, #f2f2f0);
    text-align: center;
    font-size: 18px;
    font-style: normal;
    font-weight: 800;
    line-height: normal;

    &:hover {
      background-color: #6f5fbe;
      transform: none;
    }
  }

  @media (max-width: 768px) {
    flex-flow: wrap;

    .kl-navbar__items {
      display: flex;
      width: 100%;
      justify-content: center;

      margin-top: 20px;
    }

    .kl-navbar__button {
      border: 2px solid #e7e7e7;
    }
  }
}

// FOOTER
footer {
  padding-top: 218px;
  .kl-footer__info {
    flex-basis: auto;
  }

  .kl-footer__info,
  .kl-footer__group {
    margin: 0;
    width: 164px;
  }

  .kl-footer__content {
    justify-content: flex-start;
    max-width: 940px;
    margin: auto;
  }

  .kl-footer__menu {
    gap: 40px;
  }

  .kl-footer__menu h3 {
    opacity: 0.3;
    color: #f2f2f0;
    text-decoration-line: none;
    font-size: 12px;
    font-weight: 400;
    margin-bottom: 12px;
  }

  .kl-footer__menu .kl-footer__group a {
    margin-bottom: 10px;
    opacity: 0.8;
    color: #f2f2f0;
    text-decoration: none;
    font-size: 14px;
    line-height: 20px;
    &:hover {
      opacity: 1;
    }
  }

  .kl-footer__info img {
    width: 130px !important;
    height: auto !important;
    margin-top: 26px;
  }
}
